import React, { Fragment, useState } from 'react';
import styled from 'styled-components';

import { useTranslation } from 'react-i18next';

import { DropdownMenu, IconButton, Tooltip } from '@resultadosdigitais/tangram-components';
import { Question, Apps } from '@resultadosdigitais/tangram-react-icons';

import schema from './schema';
import StyledDropdownMenuList from '../DropdownMenuList';
import DropdownMenuItem from '../DropdownMenuItem';
import NavList from '../NavList';
import NavItem from '../NavItem';

const StyledDropdownMenuListBottom = styled(StyledDropdownMenuList)`
  transform: translate3d(0, 12px, 0);
`;

const StyledTooltip = styled(Tooltip)`
  &[aria-expanded='true'] {
    button {
      background-color: var(--color-gray-10);
    }
  }
`;

const StyledIconButton = styled(IconButton.style)`
  padding-left: var(--size-spacing-02);
  padding-right: var(--size-spacing-02);

  &:focus {
    outline: none;
  }

  &:active {
    background-color: var(--color-gray-20);
  }
`;

const SecondaryNav = () => {
  const [selectedChildIndex, setSelectedChildIndex] = useState(null);
  const handleSelectedChildItem = value => setSelectedChildIndex(value);
  const { t, i18n } = useTranslation();
  const navigation = schema[i18n.language];

  return (
    <NavList data-cypress="navigation" titleLabelledby={t('secondaryNav.list.titleLabelledby')} labelledby="secondary-navigation">
      {Object.keys(navigation).map(mainMenuItem => (
        <NavItem key={mainMenuItem} secondary>
          <DropdownMenu>
            <DropdownMenu.Trigger id={`${mainMenuItem}-menu`}>
              <StyledTooltip text={t(`secondaryNav.nav.${mainMenuItem}.title`)} position={Tooltip.positions.BOTTOM}>
                <StyledIconButton kind={IconButton.kind.TERTIARY}>
                  {mainMenuItem === 'help' ? (
                    <Question title="Helper Button" titleId="idToHelperIcon" />
                  ) : (
                    <Apps title="Products Button" titleId="idToProductIcon" />
                  )}
                </StyledIconButton>
              </StyledTooltip>
            </DropdownMenu.Trigger>

            <StyledDropdownMenuListBottom>
              {navigation[mainMenuItem].child.map((subMenuItem, indexChild) => (
                <Fragment key={subMenuItem.value}>
                  {indexChild === 0 && <DropdownMenu.Title>{t(`secondaryNav.nav.${mainMenuItem}.title`)}</DropdownMenu.Title>}
                  {subMenuItem.value === 'divider' ? (
                    <DropdownMenu.Divider />
                  ) : (
                    <DropdownMenuItem
                      href={subMenuItem.path}
                      target={subMenuItem.targetBlank ? '_blank' : '_self'}
                      selected={selectedChildIndex === subMenuItem.value}
                      onClick={() => handleSelectedChildItem(subMenuItem.value)}
                    >
                      {t(`secondaryNav.nav.${mainMenuItem}.child.${subMenuItem.value}`)}
                    </DropdownMenuItem>
                  )}
                </Fragment>
              ))}
            </StyledDropdownMenuListBottom>
          </DropdownMenu>
        </NavItem>
      ))}
    </NavList>
  );
};

export default SecondaryNav;
