import { useContext, useState } from 'react'

import { FronthubContext } from 'front-hub-context-api'

const Flagger = (flag, loading) => ({ children, fallback }) => {
  if (loading) {
    return fallback || null
  } else {
    return flag ? children : null
  }
}

const isEnabled = (object = {}, features = []) => {
  return features.every(key => !!object[key])
}

const isDisabled = (object, features) => !isEnabled(object, features)

const bakeFeatureFlag = listed => featureValue => {
  const loading = typeof featureValue === 'undefined'
  const Component = Flagger(isEnabled(featureValue, listed), loading)
  Component.Enabled = Component
  Component.Disabled = Flagger(isDisabled(featureValue, listed), loading)
  return Component
}

export default function useFeature(...paramFeatures) {
  const FeatureFlag = bakeFeatureFlag([...paramFeatures])
  const { features, __configuration } = useContext(FronthubContext)
  const [lazyFeatureValue, setContextValue] = useState(features)

  console.log('lazyFeatureValue', lazyFeatureValue)
  console.log('paramFeatures', paramFeatures)

  if (typeof lazyFeatureValue === 'object' && typeof lazyFeatureValue.then !== 'function') {
    return FeatureFlag(lazyFeatureValue)
  } else if (typeof lazyFeatureValue === 'function') {
    let lazy
    try {
      lazy = lazyFeatureValue.call(void 0, __configuration)
    } catch (error) {
      console.error('front-hub: ops... there was something wrong while calling feature flag', error)
      throw error
    }
    return FeatureFlag(lazy)
  }

  Promise.resolve(lazyFeatureValue)
    .then(setContextValue)
    .catch(error => {
      console.error('front-hub: ops... there was something wrong while loading feature flag', error)
    })

  return FeatureFlag()
}
