import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import { useTranslation } from 'react-i18next';

import { Link, DropdownMenu, Button, Tag } from '@resultadosdigitais/tangram-components';

import { isPathActive } from '../../helpers';
import schema from './schema';
import DropdownMenuList from '../DropdownMenuList';
import DropdownMenuItem from '../DropdownMenuItem';
import NavList from '../NavList';
import NavItem from '../NavItem';

const StyleReset = css`
  font: var(--font-size-sm-semibold);
  padding-bottom: 22px;
  padding-top: 22px;
  color: var(--color-gray-70);

  &:hover,
  &:active {
    color: var(--color-gray-90);
    background-color: transparent;
  }

  &:focus {
    outline: none;
    color: var(--color-gray-70);
  }

  ${({ selected }) =>
    selected &&
    css`
      color: var(--color-primary-60);

      &:hover,
      &:active {
        color: var(--color-primary-70);
      }
    `}
`;

const StyledLink = styled(Link.style)`
  padding-right: var(--size-spacing-04);
  padding-left: var(--size-spacing-04);
  ${StyleReset}
`;

const StyledButton = styled(Button.style)`
  ${StyleReset}
`;

const PrimaryNav = () => {
  const [selectedIndex, setSelectedIndex] = useState(null);
  const handleSelectedItem = index => setSelectedIndex(index);
  const { t } = useTranslation();

  const renderLink = item => {
    return (
      <StyledLink
        selected={schema[item].selected.includes(window.location.pathname)}
        className="tangram-link-space"
        removeUnderline
        href={schema[item].path ? schema[item].path : '#'}
      >
        {t(`primaryNav.nav.${item}`)}
      </StyledLink>
    );
  };

  const dropdownBuilder = mainMenuItem => {
    return (
      <DropdownMenu>
        <DropdownMenu.Trigger id={mainMenuItem}>
          <StyledButton selected={isPathActive(schema[mainMenuItem].selected)} kind={Button.kind.TERTIARY}>
            {t(`primaryNav.nav.${mainMenuItem}.value`)}
          </StyledButton>
        </DropdownMenu.Trigger>

        <DropdownMenuList>
          {schema[mainMenuItem].child.map(subMenuItem => {
            return (
              <DropdownMenuItem key={subMenuItem.value} href={subMenuItem.path} selected={isPathActive(subMenuItem.selected)}>
                {t(`primaryNav.nav.${mainMenuItem}.child.${subMenuItem.value}`)}
                {subMenuItem.tags
                  ? subMenuItem.tags.map(tag => (
                      <Tag key={`${subMenuItem.value}-${tag}`} kind={Tag.kind.SUCCESS}>
                        {tag}
                      </Tag>
                    ))
                  : ''}
              </DropdownMenuItem>
            );
          })}
        </DropdownMenuList>
      </DropdownMenu>
    );
  };

  return (
    <NavList data-cypress="navigation" titleLabelledby={t('primaryNav.list.titleLabelledby')} labelledby="primary-navigation">
      {Object.keys(schema).map((mainMenuItem, index) => (
        <NavItem key={mainMenuItem} selected={index === selectedIndex} onClick={() => handleSelectedItem(index)}>
          {schema[mainMenuItem].child.length ? dropdownBuilder(mainMenuItem, index) : renderLink(mainMenuItem)}
        </NavItem>
      ))}
    </NavList>
  );
};

export default PrimaryNav;
