import React, { useContext, useState } from 'react'

import { FronthubContext } from 'front-hub-context-api'

import { parseToSafeJson } from '../../../core'

export default function withContext(Component) {
  return function LazyContext(props) {
    const { context, __configuration } = useContext(FronthubContext)
    const [lazyContextValue, setContextValue] = useState(context)
    if (typeof lazyContextValue === 'object' && typeof lazyContextValue.then !== 'function') {
      return <Component {...props} {...parseToSafeJson(lazyContextValue)} />
    } else if (typeof lazyContextValue === 'function') {
      let lazy
      try {
        lazy = lazyContextValue.call(null, __configuration)
      } catch (error) {
        console.error('front-hub: ops... there was something wrong while calling application context', error)
        throw error
      }
      return <Component {...props} {...parseToSafeJson(lazy)} />
    }
    Promise.resolve(lazyContextValue)
      .then(setContextValue)
      .catch(error => {
        console.error('front-hub: ops... there was something wrong while loading application context', error)
      })
    return null
  }
}
