import React from 'react';
import { withContext } from '@resultadosdigitais/front-hub/react';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { Logo, Theme } from '@resultadosdigitais/tangram-components';
import { LogoMarketing } from '@resultadosdigitais/tangram-react-icons';

import InteropStyle from './components/InteropStyle';
import PrimaryNav from './components/PrimaryNav';
import SecondaryNav from './components/SecondaryNav';
import AccountMenu from './components/AccountMenu';

const Header = styled.header`
  display: flex;
  width: 100%;
  justify-content: space-between;

  background-color: var(--color-white);
  box-shadow: var(--shadow-sm);

  .wrapper {
    display: flex;

    &:last-child ul {
      margin-right: var(--size-spacing-04);
    }
  }
`;

const NavBar = withContext(({ user }) => {
  const { account } = user;
  const { t } = useTranslation();

  return (
    <Theme as="div">
      <InteropStyle />
      <Header>
        <div className="wrapper">
          <Logo data-cypress="navigation" rel="noopener noreferrer" hint={t(`goToHomePage`)} href="/">
            <LogoMarketing title={`${t(`goToHomePage`)} | RDStation Marketing`} titleId="rdstation-marketing" />
          </Logo>
          <PrimaryNav />
        </div>

        <div className="wrapper">
          <SecondaryNav />
          {user && <AccountMenu data-cypress="navigation" userName={user.name} accountName={account.name} accountId={account.id} />}
        </div>
      </Header>
    </Theme>
  );
});

export default NavBar;
