import privateContextApi from '../comunication-api'

export const methods = ['dispatchEvent', 'delegate', 'registerDelegate', 'addEventListener']

const makeAvaliable = ({ configuration = {} }, prop) => configuration[prop] || {}

export default function contextPublicApiFactory(fronthubInstance) {
  const privateApi = privateContextApi(fronthubInstance)
  return methods.reduce(
    (acc, cur) => ({
      ...acc,
      [cur]: privateApi[cur],
    }),
    {
      context: makeAvaliable(fronthubInstance, 'context'),
      features: makeAvaliable(fronthubInstance, 'features'),
    },
  )
}
